import { // 
	auth
} from './api.js'
import Config from '@/utils/config.js'
import Request from '../index.js'

const http = new Request({
	validateStatus: (statusCode) => { // statusCode 
		return (statusCode >= 200 && statusCode < 300) || statusCode == 401
	}
})
http.setConfig((config) => { /*  */
	config.baseURL = Config.apiUrl
	config.header = {
		...config.header,
		ContentType: 'application/json;charset=UTF-8',
		ContentType: 'application/x-www-form-urlencoded',
		'Accept-Language': 'zh-CN,zh',
		'X-Requested-With': 'XMLHttpRequest'
	}
	return config
})

http.interceptors.request.use((config) => { /* async await  */
	config.header = {
		...config.header,
		'token': getTokenStorage()
	}
	/*
	 if (!token) { // tokenreturn Promise.reject(config) 
	   return Promise.reject(config)
	 }
	 */
	return config
}, (config) => {
	return Promise.reject(config)
})

http.interceptors.response.use(async (response) => { /* async await   */
	// if (response.data.code !== 200) { // 200reject()
	//   return Promise.reject(response)
	// }

	const {
		statusCode,
		config
	} = response
	try {
		return await handleCode(response, statusCode, config)
	} catch (err) {
		return Promise.reject(err)
	}

	return response
}, (response) => { // async await 
	console.log(response)
	return Promise.reject(response)
})


/**
 *  token
 * @return {string}
 */
const getTokenStorage = () => {
	return uni.getStorageSync('equipment_token') || ''
}

/**
 *  `token`
 * @param {number} uid
 * @return {Promise}
 */
const getApiToken = () => {
	let params = { // 
		code: uni.getStorageSync('equipment_code') || '',
		openid: uni.getStorageSync('equipment_openid') || '',
		token: uni.getStorageSync('equipment_token') || '', 
	}
	return auth(params).then((res) => {
		return res.data
	})
}

/**
 *  token  localStorage
 * @param {object} data
 */
const saveTokenOpenid = (data) => {
	uni.setStorageSync('equipment_token', data.token)
	if (data.openid != '') {
		uni.setStorageSync('equipment_openid', data.openid)
	}
}

/**
 *  http
 * @param {object} o
 * @param {object} o.res 
 * @param {object} o.config config
 * @param {string|number} o.statusCode http
 * @return {object|Promise<reject>}
 */
const handleCode = (res, statusCode, config) => {
	const STATUS = {
		'200'() {
			if (res.data.code == 1) {
				return res.data
			} else {
				return Promise.reject(res.data)
			}
		},
		'401'() {
			// 
			if (!config.hasOwnProperty("count")) {
				config.count = 0
			}

			// code401
			if (config.count === 1) {
				return Promise.reject(res)
			}

			config.count++; // count

			return getApiToken()
				.then(saveTokenOpenid)
				.then(() => http.request(config))
		}
	}

	return STATUS[statusCode] ? STATUS[statusCode]() : Promise.reject(res) //  `fail`
}

export {
	http
}
