import {
	http
} from './service.js'

/**
 * 
 * @param {Object} params
 */
export const auth = (params) => {
	// code, code
	if (params.code) {
		wx.login({
			success(res) {
				if (res.code) {
					uni.setStorageSync('equipment_code', res.code)
				}
			}
		})
	}

	return http.post('user/weapplogin', params)
}

